.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

.link-reset {
  margin: 0;
  color: inherit;
  text-decoration: none;
  cursor: pointer;
}

.button-reset {
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
